$(document).ready(function () {







  $(".order-js").on("click", function (event) {
    event.preventDefault();
    var id = $(this).attr('href'),
      top = $(id).offset().top;
    $('body,html').animate({
      scrollTop: top
    }, 1500);
  });






  var formHeight = $(".log-in__col--form").height();
  var win = $(window).height();

  if (formHeight > win) {
    formHeight = win / 1;
    $('.log-in__col--form').css({
      'height': formHeight
    });
    $('.log-in__col form').css({
      'padding': '20px 10px 10px 0',
      'overflow-y': 'scroll'
    });
  }



  $('.ct-ignition__button.ct-ignition__button--edit').on('click', function () {
    $('.calculator__item-description').addClass('description-fade');
  });


  $('.input .eye .close').on('click', function () {
    $(this).parent().parent().find('input').attr('type', 'text');
    $(this).parent().find('.open').show();
    $(this).hide();
  });

  $('.input .eye .open').on('click', function () {
    $(this).parent().parent().find('input').attr('type', 'password');
    $(this).parent().find('.close').show();
    $(this).hide();
  });


});